	module.exports = {"data":{"sidebar":{"fields":{"yml":{"items":[{"title":"Presentation","link":"/workshops/jest/presentation/"},{"title":"Workshop Goals","link":"/workshops/jest/workshop-goals"},{"title":"Features","link":"/workshops/jest/features/"},{"title":"Setup","link":"/workshops/jest/setup/"},{"title":"Basic tests","link":"/workshops/jest/basic-tests/"},{"title":"Babel","link":"/workshops/jest/babel/"},{"title":"Globals and Matchers","link":"/workshops/jest/globals-and-matchers/"},{"title":"Asychronous Testing","link":"/workshops/jest/asynchronous-testing/"},{"title":"Mocks","link":"/workshops/jest/mocks/"},{"title":"React","link":"/workshops/jest/react/"}]}}},"workshop":{"html":"<h2 id=\"getting-started\"><a href=\"#getting-started\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Getting started</h2>\n<p>We will use Yarn to install Jest as a JavaScript dependency.</p>\n<p>Firstly ensure that that you have yarn available:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ yarn --version\nv1.3.2\n</code></pre>\n      </div>\n<p>If you do not have yarn visit <a href=\"https://yarnpkg.com/en/docs/install\">installing Yarn</a> for\nfurther details on how to install Yarn on your operating system. At the time of writing,\nif you wish to install Yarn via homebrew:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">brew <span class=\"token function\">install</span> yarn\n</code></pre>\n      </div>\n<h3 id=\"creating-a-new-project\"><a href=\"#creating-a-new-project\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Creating a new project</h3>\n<p>Firstly create a new directory where you want to run your project:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\"><span class=\"token function\">mkdir</span> ~/Documents/jest-workshop\n<span class=\"token function\">cd</span> ~/Documents/jest-workshop\n</code></pre>\n      </div>\n<p>We can initialize a new <code>package.json</code> that will specify all of our project dependencies\nvia the <code>yarn init</code> command. You will asked various questions about this project, the\nanswers are not too important and can be left as their default value for the most part:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">yarn init\n</code></pre>\n      </div>\n<p>This will generate a new <code>package.json</code> file similar to:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-json\"><code class=\"language-json\"><span class=\"token punctuation\">{</span>\n  <span class=\"token property\">\"name\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"jest-workshop\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"version\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"1.0.0\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"main\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"index.js\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"license\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"MIT\"</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>Now we can install jest:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">yarn add --dev jest\n</code></pre>\n      </div>\n<p>This command has also modified your <code>package.json</code> file:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-json\"><code class=\"language-json\"><span class=\"token punctuation\">{</span>\n  <span class=\"token property\">\"name\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"jest-workshop\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"version\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"1.0.0\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"main\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"index.js\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"license\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"MIT\"</span><span class=\"token punctuation\">,</span>\n<span class=\"gatsby-highlight-code-line\">  <span class=\"token property\">\"devDependencies\"</span><span class=\"token operator\">:</span> <span class=\"token punctuation\">{</span>\n</span><span class=\"gatsby-highlight-code-line\">    <span class=\"token property\">\"jest\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"^22.4.2\"</span>\n</span><span class=\"gatsby-highlight-code-line\">  <span class=\"token punctuation\">}</span>\n</span><span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>Let's define a script within our <code>package.json</code> file to run our test suite via jest:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-json\"><code class=\"language-json\"><span class=\"token punctuation\">{</span>\n  <span class=\"token property\">\"name\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"jest-workshop\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"version\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"1.0.0\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"main\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"index.js\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"license\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"MIT\"</span><span class=\"token punctuation\">,</span>\n<span class=\"gatsby-highlight-code-line\">  <span class=\"token property\">\"scripts\"</span><span class=\"token operator\">:</span> <span class=\"token punctuation\">{</span>\n</span><span class=\"gatsby-highlight-code-line\">    <span class=\"token property\">\"test\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"jest\"</span>\n</span><span class=\"gatsby-highlight-code-line\">  <span class=\"token punctuation\">}</span>\n</span>  <span class=\"token property\">\"devDependencies\"</span><span class=\"token operator\">:</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token property\">\"jest\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"^22.4.2\"</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>We can now run our JavaScript tests with the <code>yarn run</code> command:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">$ yarn run <span class=\"token function\">test</span>\nyarn run v1.3.2\n$ jest\nNo tests found\nIn ~/Documents/jest-workshop\n  1 <span class=\"token function\">file</span> checked.\n  testMatch: **/__tests__/**/*.js?<span class=\"token punctuation\">(</span>x<span class=\"token punctuation\">)</span>,**/?<span class=\"token punctuation\">(</span>*.<span class=\"token punctuation\">)</span><span class=\"token punctuation\">(</span>spec<span class=\"token operator\">|</span>test<span class=\"token punctuation\">)</span>.js?<span class=\"token punctuation\">(</span>x<span class=\"token punctuation\">)</span> - 0 matches\n  testPathIgnorePatterns: /node_modules/ - 1 match\nPattern:  - 0 matches\nerror Command failed with <span class=\"token keyword\">exit</span> code 1.\ninfo Visit https://yarnpkg.com/en/docs/cli/run <span class=\"token keyword\">for</span> documentation about this command.\n</code></pre>\n      </div>\n<p>Oops, no tests? Let's add some tests!</p>","headings":[{"value":"Getting started"},{"value":"Creating a new project"}],"frontmatter":{"title":"Jest workshop - Setup","date":null},"fields":{"slug":"/workshops/jest/setup/"}}},"pathContext":{"slug":"/workshops/jest/setup/"}}

